main {
    min-height: 80vh;
}

html,body { 
  height:100%; 
  font-family: 'Poppins', sans-serif;
 }
.maxwidth{
  max-width: 1200px;
  margin: 0 auto;
}
.navbar-brand{
  font-size: 1.6rem;
}


.navbar .menu_item , .navbar-brand{ 
  background-color: bisque ;
  padding: 3px 10px;
}

.navbar .menu_item:hover {    
    transition: 0.4s;
    color: rgb(209, 209, 209);
}

.banner{
    min-height: inherit;
}

.banner::before{
  content: "";  
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: linear-gradient(120deg, #3f3f3f81, #2e2e2e81);  
  z-index: 1;
}
.topbanner{
  position: relative;
  z-index: 3;
  opacity: 3;
}

.navbar a{
    transition: 0.5s; 
}
.text-white:hover{
  color: rgb(209, 209, 209);
}
 
.header_input{
  max-width: 50%;
  min-width: 400px;
  margin: 2rem auto;
}

.bold{
  font-weight: 500;
}

.home-screen{
  position: relative;
  min-height: 92vh;
  z-index: 3;
}
 

.text-justify{
  text-align: justify;
}

.grph2{
  align-self: center;
  line-height: 1.8em;
}
.about h4, h3, p {
  line-height: 1.8em;
}

.mw-50{
  max-width: 40%;
  min-width: 400px
}

.slideanim {visibility:hidden;}

.fade-in-up, .fade-in-right, .fade-in-left{
  animation-duration: 1s;
  -webkit-animation-duration: 1s;
  visibility: visible!important;
}

.fade-in-up{
  animation-name: slideUp;
  -webkit-animation-name: slideUp; 
}


.fade-in-right{
  animation-name: slideright;
  -webkit-animation-name: slideright; 
}


.fade-in-left{
  animation-name: slideleft;
  -webkit-animation-name: slideleft; 
}

/*MY ANIMATIONS*/
@keyframes slideUp {
  0% {
    opacity: 0;
    transform: translateY(70%);
  } 
  100% {
    opacity: 1;
    transform: translateY(0%);
  }
}
@-webkit-keyframes slideUp {
  0% {
    opacity: 0;
    -webkit-transform: translateY(70%);
  } 
  100% {
    opacity: 1;
    -webkit-transform: translateY(0%);
  }
}


@keyframes slideright {
  0% {
    opacity: 0;
    transform: translateY(70%);
  } 
  100% {
    opacity: 1;
    transform: translateY(0%);
  }
}
@-webkit-keyframes slideright {
  0% {
    opacity: 0;
    -webkit-transform: translateY(70%);
  } 
  100% {
    opacity: 1;
    -webkit-transform: translateY(0%);
  }
}



@keyframes slideleft {
  0% {
    opacity: 0;
    transform: translateX(70%);
  } 
  100% {
    opacity: 1;
    transform: translateX(0%);
  }
}
@-webkit-keyframes slideleft {
  0% {
    opacity: 0;
    -webkit-transform: translateX(70%);
  } 
  100% {
    opacity: 1;
    -webkit-transform: translateX(0%);
  }
}


h2 {
  font-size: 24px;
  text-transform: uppercase;
  color: #303030;
  font-weight: 600;
  margin-bottom: 30px;
}
h4 {
  font-size: 19px;
  line-height: 1.375em;
  color: #303030;
  font-weight: 400;
  margin-bottom: 30px;
}  
.jumbotron {
  background-color: #f4511e;
  color: #fff;
  padding: 100px 25px;
  /* font-family: Montserrat, sans-serif; */
}
.container-fluid {
  padding: 60px 50px;
}
.bg-grey {
  background-color: #f6f6f6;
}
.logo-small {
  color: #f4511e;
  font-size: 50px;
}
.logo {
  color: #f4511e;
  font-size: 200px;
}
.thumbnail {
  padding: 0 0 15px 0;
  border: none;
  border-radius: 0;
}
.thumbnail img {
  width: 100%;
  height: 100%;
  margin-bottom: 10px;
}
.carousel-control.right, .carousel-control.left {
  background-image: none;
  color: #f4511e;
}
.carousel-indicators li {
  border-color: #f4511e;
}
.carousel-indicators li.active {
  background-color: #f4511e;
}
.item h4 {
  font-size: 19px;
  line-height: 1.375em;
  font-weight: 400;
  font-style: italic;
  margin: 70px 0;
}
.item span {
  font-style: normal;
}
.panel {
  border: 1px solid #f4511e; 
  border-radius:0 !important;
  transition: box-shadow 0.5s;
}
.panel:hover {
  box-shadow: 5px 0px 40px rgba(0,0,0, .2);
}
.panel-footer .btn:hover {
  border: 1px solid #f4511e;
  background-color: #fff !important;
  color: #f4511e;
}
.panel-heading {
  color: #fff !important;
  background-color: #f4511e !important;
  padding: 25px;
  border-bottom: 1px solid transparent;
  border-top-left-radius: 0px;
  border-top-right-radius: 0px;
  border-bottom-left-radius: 0px;
  border-bottom-right-radius: 0px;
}
.panel-footer {
  background-color: white !important;
}
.panel-footer h3 {
  font-size: 32px;
}
.panel-footer h4 {
  color: #aaa;
  font-size: 14px;
}
.panel-footer .btn {
  margin: 15px 0;
  background-color: #f4511e;
  color: #fff;
}
.navbar {
  margin-bottom: 0; 
  z-index: 9999;
  border: 0;
  font-size: 12px !important;
  line-height: 1.42857143 !important;
  letter-spacing: 4px;
  border-radius: 0;
  /*font-family: Montserrat, sans-serif;*/
}
.navbar li a, .navbar .navbar-brand {
  color: #fff !important;
}
.navbar-nav li a:hover, .navbar-nav li.active a {
  color: #f4511e !important;
  background-color: #fff !important;
}
.navbar-default .navbar-toggle {
  border-color: transparent;
  color: #fff !important;
}
footer .glyphicon {
  font-size: 20px;
  margin-bottom: 20px;
  color: #f4511e;
}
.slideanim {visibility:hidden;}
.slide {
  animation-name: slide;
  -webkit-animation-name: slide;
  animation-duration: 1s;
  -webkit-animation-duration: 1s;
  visibility: visible;
} 
@media screen and (max-width: 768px) {
  .col-sm-4 {
    text-align: center;
    margin: 25px 0;
  }
  .btn-lg {
    width: 100%;
    margin-bottom: 35px;
  }
}
@media screen and (max-width: 480px) {
  .logo {
    font-size: 150px;
  }
}